@charset "UTF-8";
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  color: #2c2b2b;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  font-family: "Oswald", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1220px;
  padding: 0px 10px;
  margin: 0px auto;
}

.header {
  background-image: url(../images/header-background.jpg);
  background-color: #21062a;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  min-height: 700px;
  color: #fff;
}
.header__top {
  padding: 47px 0 45px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__content {
  padding-top: 60px;
  position: relative;
}
.header__title {
  font-weight: 400;
  font-size: 72px;
  line-height: 107px;
  text-transform: uppercase;
}
.header__subtitle {
  font-weight: 400;
  font-size: 36px;
  line-height: 53px;
  text-transform: uppercase;
  margin-bottom: 30px;
}
.header__text {
  margin-bottom: 30px;
  max-width: 800px;
}
.header__social {
  padding: 70px 0 20px 70px;
}
.header__images {
  position: absolute;
  right: 0;
  bottom: -270px;
}

.social {
  display: flex;
  align-items: center;
}
.social__link {
  margin: 0px 5px;
}

.phone {
  font-weight: 400;
  font-size: 36px;
  line-height: 53px;
  text-transform: uppercase;
}

.button {
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  padding: 14px 54px 15px;
  background-color: #5a0d67;
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
.button:hover {
  background-color: #fff;
  color: #5a0d67;
}
.button--decor {
  margin-top: -7px;
  color: #fff;
  border: 1px solid #5a0d67;
  position: relative;
}
.button--decor::after {
  content: "ЖМИ";
  color: #c4c4c4;
  position: absolute;
  margin-left: 130px;
}
.button--decor::before {
  content: "";
  position: absolute;
  width: 80px;
  height: 2px;
  background-color: #c4c4c4;
  right: -65px;
  top: 24px;
}

.title {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 54px;
  text-transform: uppercase;
  color: #21062a;
  margin-bottom: 50px;
  position: relative;
}
.title::after {
  content: "";
  width: 80px;
  height: 3px;
  background-color: #21062a;
  position: absolute;
  top: 20px;
  margin-left: 20px;
}

.services {
  padding-top: 350px;
}
.services__inner {
  display: flex;
  margin-bottom: 100px;
}
.services__content {
  width: 505px;
  margin-right: 96px;
}
.services__content-box {
  margin-bottom: 57px;
}
.services__content-title {
  color: #5a0d67;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  margin-bottom: 12px;
}
.services__content-text {
  margin-bottom: 12px;
}
.services__list {
  list-style: none;
  counter-reset: my-counter;
}
.services__item {
  position: relative;
  counter-increment: my-counter;
  border: 2px solid #c4c4c4;
  margin-bottom: 12px;
  width: 450px;
  padding: 10px 15px 10px 113px;
  transition: all 0.5s ease-in-out;
}
.services__item::before {
  content: "0" counter(my-counter);
  position: absolute;
  left: 20px;
  font-weight: 400;
  font-size: 36px;
  line-height: 53px;
  text-transform: uppercase;
  color: #c4c4c4;
  transform: rotate(-90deg);
}
.services__item:hover {
  border: 2px solid #5a0d67;
  width: 500px;
}
.services__item:hover .services__item-title {
  color: #5a0d67;
}
.services__item:hover::before {
  color: #5a0d67;
}
.services__item-title {
  font-weight: 300;
  font-size: 24px;
}

.benefits {
  margin-bottom: 100px;
}
.benefits__inner {
  display: flex;
}
.benefits__images {
  margin-right: 95px;
}
.benefits__content {
  width: 100%;
}
.benefits__title {
  margin-left: 185px;
}
.benefits__list {
  list-style: none;
  background-color: #21062a;
  padding: 25px 0 25px 185px;
  color: #fff;
}
.benefits__item {
  width: 320px;
  margin-bottom: 20px;
}
.benefits__item-num {
  font-weight: 400;
  font-size: 36px;
  line-height: 53px;
  text-transform: uppercase;
}
.benefits__item-title {
  font-weight: 300;
  font-size: 24px;
  line-height: 36px;
}
.benefits__item-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
  color: #c4c4c4;
}

.carousel {
  margin-bottom: 100px;
}
.carousel__inner {
  margin: 0px -7px;
}
.carousel__item {
  margin: 0px 7px;
}
.carousel__item-box {
  border: 1px solid #c4c4c4;
  padding: 6px 6px 16px;
  text-align: center;
  transition: all 0.5s ease-out;
}
.carousel__item-box:hover {
  color: white;
  background-color: #5a0d67;
  border: 1px solid #5a0d67;
}
.carousel__item-img {
  width: 100%;
  margin-bottom: 23px;
}
.carousel__item-title {
  font-weight: 300;
  margin-bottom: 5px;
}
.carousel__item-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
}
.carousel .slick-dots {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  list-style: none;
}
.carousel .slick-dots button {
  font-size: 0;
  border: none;
  padding: 0;
  margin: 0 10px;
  background-color: #c4c4c4;
  width: 50px;
  height: 1px;
}
.carousel .slick-active button {
  background-color: #5a0d67;
  height: 3px;
}

.contacts__inner {
  display: flex;
  justify-content: space-between;
}
.contacts__list {
  list-style: none;
}
.contacts__item {
  margin-bottom: 20px;
}
.contacts__item-title {
  color: #5a0d67;
}
.contacts__form {
  background-color: #21062a;
  padding: 50px 75px 57px;
  text-align: center;
}
.contacts__title {
  color: #fff;
  margin: 0 0 16px 53px;
  text-align: left;
}
.contacts__title:after {
  background-color: #fff;
}
.contacts__input {
  background-color: transparent;
  width: 450px;
  height: 75px;
  color: #fff;
  border: 1px solid #fff;
  margin-bottom: 12px;
  display: block;
  font-size: 24px;
  padding: 0 15px;
  text-align: center;
}
.contacts__input::placeholder {
  text-align: center;
  font-style: 18px;
  line-height: 27px;
  font-weight: 300;
  color: #c4c4c4;
  font-family: "Oswald";
  font-style: normal;
}
.contacts__btn {
  border: none;
  font-weight: 300;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 78px;
  cursor: pointer;
}

.footer {
  background-color: #21062a;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  height: 100px;
}

@media (max-width: 1140px) {
  .services__item:hover {
    width: 450px;
  }
  .services__list {
    order: -1;
    margin-bottom: 30px;
    width: 100%;
  }
  .services__item {
    width: 100%;
  }
  .services__item:hover {
    width: 100%;
  }
  .services__content {
    width: 100%;
    margin-right: 0;
  }
}
@media (max-width: 1024px) {
  .header__images {
    width: 80%;
  }

  .services {
    padding-top: 250px;
  }
  .services__inner {
    flex-wrap: wrap;
  }

  .benefits__images {
    display: none;
  }
}
@media (max-width: 900px) {
  .phone {
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
  }

  .header__title {
    font-size: 54px;
    line-height: 74px;
  }
  .header__subtitle {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 20px;
  }
  .header__social {
    padding: 30px 0 20px 53px;
  }

  .contacts__inner {
    display: block;
    text-align: center;
  }
  .contacts__list {
    margin-bottom: 30px;
  }
  .contacts__input {
    width: 100%;
  }
  .contacts__btn {
    margin-top: 20px;
  }
  .contacts__title {
    text-align: center;
    margin-left: 0;
  }
}
@media (max-width: 768px) {
  .benefits__inner {
    text-align: center;
  }
  .benefits__title {
    margin-left: 0px;
  }
  .benefits__list {
    padding: 25px 15px;
  }
  .benefits__item {
    width: 100%;
  }
  .benefits__item-num {
    font-size: 24px;
    line-height: 36px;
  }
}
@media (max-width: 650px) {
  .footer__inner {
    flex-wrap: wrap;
    justify-content: center;
    height: auto;
    padding: 20px 0;
    text-align: center;
  }
  .footer .social {
    width: 100%;
    justify-content: center;
    margin: 25px 0px;
  }
}
@media (max-width: 600px) {
  .header__content {
    padding-top: 0;
    text-align: center;
  }
  .header__title {
    font-size: 54px;
    line-height: 74px;
  }
  .header__social {
    padding: 30px 0 20px 0px;
  }
  .header .social {
    justify-content: center;
  }

  .services {
    padding-top: 150px;
  }
  .services .title {
    font-size: 28px;
    line-height: 36px;
    margin-bottom: 30px;
  }
  .services__item::before {
    top: 5px;
  }
  .services__content {
    text-align: center;
  }

  .contacts__title::after {
    display: none;
  }

  body {
    font-size: 16px;
    line-height: 22px;
  }
}
@media (max-width: 520px) {
  .header__top {
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }
  .header__title {
    margin-bottom: 20px;
  }
  .header__images {
    width: 100%;
    position: static;
  }

  .logo {
    margin-bottom: 20px;
    width: 100%;
  }

  .services {
    padding-top: 50px;
  }
  .services__item {
    padding-left: 60px;
  }
  .services__item::before {
    left: 11px;
  }
  .services__inner {
    margin-bottom: 50px;
  }

  .button--decor::before {
    right: -50px;
  }
  .button--decor::after {
    margin-left: 110px;
  }

  .benefits {
    margin-bottom: 50px;
  }

  .button--decor::before, .button--decor::after {
    display: none;
  }

  .carousel {
    margin-bottom: 50px;
  }
  .carousel .slick-dots button {
    width: 10px;
  }
  .carousel__title::after {
    display: none;
  }

  .contacts__form {
    padding: 50px 10px 55px;
  }
}
@media (max-width: 360px) {
  .title {
    text-align: center;
  }
  .title::after {
    display: none;
  }
}
@media (max-width: 340px) {
  .header__title {
    font-size: 45px;
    line-height: 50px;
  }
}